import SignupForm from "components/SignupForm";
import Button from "react-bootstrap/Button";
import { ResultItem, ResultsCont, ReviewButton } from "./styled";

const Result = ({
    isPage,
    resetQuiz,
    totalCorrect,
    onReview,
}: {
    isPage: boolean;
    resetQuiz: () => void;
    totalCorrect: Number;
    onReview: () => void;
}) => (
    <ResultsCont isPage={isPage}>
        <ResultItem>Total correct: {totalCorrect}</ResultItem>
        {!isPage && (
            <ResultItem>
                <SignupForm />
            </ResultItem>
        )}
        {isPage && (
            <ResultItem>
                <ReviewButton variant="link" onClick={onReview}>
                    Review all questions
                </ReviewButton>
            </ResultItem>
        )}
        <ResultItem>
            <Button onClick={resetQuiz} variant="link">
                Try again
            </Button>
        </ResultItem>
    </ResultsCont>
);

export default Result;
