import Form from "react-bootstrap/Form";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { useState } from "react";

import { GetStartedButton, SignUpFormCont, PasswordCol } from "./styled";
import { Link } from "react-router-dom";

const SignUpForm = () => {
    const [hidePasswordField, sethidePasswordField] = useState(true);
    const onEmailPress = () => hidePasswordField && sethidePasswordField(false);

    return (
        <SignUpFormCont>
            <form method="get">
                <Form.Group>
                    <Row>
                        <Col md={hidePasswordField ? 3 : 2} />
                        <Col md={3}>
                            <Form.Control
                                type="email"
                                required="required"
                                placeholder="Email"
                                name="email"
                                size="md"
                                onKeyPress={onEmailPress}
                                onChange={onEmailPress}
                            />
                        </Col>

                        <PasswordCol md={3} isHidden={hidePasswordField}>
                            <Form.Control
                                type="password"
                                required="required"
                                placeholder="Password"
                                size="md"
                                name="password"
                            />
                        </PasswordCol>
                        <Col md={3}>
                            <Link
                                to={`/quiz/${Math.round(Math.random() * 1000)}`}
                            >
                                <GetStartedButton variant="primary" size="md">
                                    Signup with Email!
                                </GetStartedButton>
                            </Link>
                        </Col>
                    </Row>
                </Form.Group>
            </form>
        </SignUpFormCont>
    );
};

export default SignUpForm;
